import React, { FC, useEffect, useRef, useState } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Button, Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, WindowComponent } from 'summer';
import { CLASS_NAME, CustomizacaoTermos } from '../../../../models/dtos/mfp/customizacaoTermos';
import { CUSTOMIZACAO_TERMOS_TAGS_QUITACAO_ANUAL_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermoQuitacaoAnualEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_ABONO_PECUNIARIO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsAbonoPecuniarioEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_ACORDO_COMPENSACAO_HORAS_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsAcordoCompensacaoHorasEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_ACORDO_PRORROGADO_HORA_EXTRA_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsAcordoProrrogacaoHoraExtraEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_ANIVERSARIO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsAniversarioEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_AVISO_FERIAS_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsAvisoFeriasEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_AVISO_PREVIO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsAvisoPrevioEnum';
import { CUSTUMIZACAO_TERMOS_TAGS_CARTA_ADVERTENCIA_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsCartaAdvertenciaEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_CARTA_PREPOSICAO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsCartaPreposicaoEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_CARTA_RECOMENDACAO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsCartaRecomendacaoEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_CARTA_SUSPENSAO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsCartaSuspensaoEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_CONTRA_CHEQUE_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsContraChequeEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_CONTRATO_ESTAGIO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsContratoEstagioEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_CONTRATO_EXPERIENCIANORMAL_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsContratoExperienciaNormalEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_CONTRATO_TRABALHO_IMTERMITENTE_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsContratoTrabalhoImtermitenteEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_CONTRATO_TRABALHO_MENOR_APRENDIZ_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsContratoTrabalhoMenorAprendizEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_CONTRATO_TAB_TEMPO_DETERMINADO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsContratoTrabTempoDeterminadoEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_CONTRATO_TRAB_TEMPO_INDETERMINADO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsContratoTrabTempoIndeterminadoEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_CONTRATO_TRAB_TEMPO_PARCIAL_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsContratoTrabTempoParcialEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_DECLARACAO_OUTRO_VINCULO_EMPREGATICIO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsDeclaracaoOutroVinculoEmpregaticioEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_EMPRESTIMOS_EPI_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsEmprestimoEPIEnum';
import { CUSTUMIZACAO_TERMOS_TAGS_OPCAO_FGTS_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsOpcaoFgtsEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_RECIBO_CARTEIRA_TRABALHO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsReciboCarteiraTrabalhoEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_RECIBO_CESTA_BASICA_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsReciboCestaBasicaEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_RECIBO_PROLABORE_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsReciboProLaboreEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_TERMO_ADITIVO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsTermoAditivoEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_VALE_ALIMENTACAO_RECIBO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsValeAlimentacaoReciboEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_VALE_REFEICAO_RECIBO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsValeRefeicaoReciboEnum';
import { CUSTUMIZACAO_TERMOS_TAGS_VALE_TRANSPORTE_DESISTENCIA_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsValeTransporteDesistenciaEnum';
import { CUSTOMIZACAO_TERMOS_TAGS_VALE_TRANSPORTE_RECIBO_ENUM_VALUES } from '../../../../models/enumerated/mfp/customizacaoTermosTagsValeTransporteReciboEnum';
import {
  ABONO_PECUNIARIO,
  ACORDO_PRORROGACAO_HORA_EXTRA,
  AVISO_FERIAS,
  AVISO_PREVIO_ACORDO_INDENIZADO,
  AVISO_PREVIO_ACORDO_TRABALHADO,
  AVISO_PREVIO_INDENIZADO,
  AVISO_PREVIO_TRABALHADO,
  CARTA_ADVERTENCIA,
  CARTA_ADVERTENCIA_FALTA,
  CARTA_PREPOSICAO,
  CARTA_RECOMENDACAO,
  CARTA_SUSPENSAO,
  CONTRATO_ESTAGIO,
  CONTRATO_EXPERIENCIA_NORMAL,
  CONTRATO_TRABALHO_INTERMITENTE,
  CONTRATO_TRABALHO_MENOR_APRENDIZ,
  CONTRATO_TRABALHO_TEMPO_DETERMINADO,
  CONTRATO_TRABALHO_TEMPO_INDETERMINADO,
  CONTRATO_TRABALHO_TEMPO_PARCIAL,
  CUSTOMIZACAO_TERMO_ENUM_VALUES,
  DECLARACAO_MAIS_DE_UM_VINCULO_EMPREGATICIO,
  DECLARACAO_MAIS_DE_UM_VINCULO_EMPREGATICIO_AUTOMATIZADO,
  DECLARACAO_OPCAO_FGTS,
  EMPRESTIMO_EPI,
  getTermoDefault,
  MSG_ANIVERSARIO,
  MSG_CONTRA_CHEQUE,
  PEDIDO_DEMISSAO,
  PEDIDO_DEMISSAO_ACORDO,
  RECIBO_CARTEIRA_DE_TRABALHO_DEVOLUCAO,
  RECIBO_CARTEIRA_DE_TRABALHO_ENTREGA,
  RECIBO_CESTA_BASICA,
  RECIBO_PRO_LABORE,
  TERMO_ADITIVO,
  TERMO_COMPENSACAO_HORAS,
  TERMO_QUITACAO_ANUAL,
  VALE_ALIMENTACAO_RECIBO,
  VALE_REFEICAO_RECIBO,
  VALE_TRANSPORTE_DESISTENCIA,
  VALE_TRANSPORTE_RECIBO,
} from '../../../../models/enumerated/mfp/customizacaoTermosTipoEnum';

interface CustomizacaoTermosViewProps extends EditViewProps<CustomizacaoTermos> {}

const CustomizacaoTermosEditView: FC<CustomizacaoTermosViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<CustomizacaoTermos>({ defaultValues: selectedItem, mode: 'onBlur' });

  const [isTagTermos, setIsTagTermos] = useState(CUSTOMIZACAO_TERMOS_TAGS_AVISO_PREVIO_ENUM_VALUES);

  const [isTag, setIsTag] = useState(null);

  const conteudoRef = useRef<HTMLTextAreaElement>();

  const funcInsertTaginState = (valor: any) => {
    setIsTag(valor);
  };

  const restaurarTermoPadrao = () => {
    if (formProps.getValues().tipo !== null) {
      const result = getTermoDefault(formProps.getValues().tipo);
      if (result.valueOf() === null) {
        formProps.setValue('conteudo', ' ');
      } else {
        formProps.setValue('conteudo', result);
      }
    }
  };

  const funcInsertTagTermoWithComboBox = () => {
    const termoA = formProps.getValues().conteudo;
    const { selectionStart } = conteudoRef.current;
    const resultStr = termoA.substring(0, selectionStart) + isTag;
    formProps.setValue('conteudo', resultStr);
  };

  useEffect(
    () => {
      switch (formProps.getValues().tipo) {
        case AVISO_PREVIO_TRABALHADO.value:
        case AVISO_PREVIO_INDENIZADO.value:
        case PEDIDO_DEMISSAO.value:
        case AVISO_PREVIO_ACORDO_TRABALHADO.value:
        case AVISO_PREVIO_ACORDO_INDENIZADO.value:
        case PEDIDO_DEMISSAO_ACORDO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_AVISO_PREVIO_ENUM_VALUES);
          break;
        case TERMO_COMPENSACAO_HORAS.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_ACORDO_COMPENSACAO_HORAS_ENUM_VALUES);
          break;
        case CONTRATO_EXPERIENCIA_NORMAL.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_CONTRATO_EXPERIENCIANORMAL_ENUM_VALUES);
          break;
        case TERMO_ADITIVO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_TERMO_ADITIVO_ENUM_VALUES);
          break;
        case VALE_TRANSPORTE_RECIBO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_VALE_TRANSPORTE_RECIBO_ENUM_VALUES);
          break;
        case VALE_TRANSPORTE_DESISTENCIA.value:
          setIsTagTermos(CUSTUMIZACAO_TERMOS_TAGS_VALE_TRANSPORTE_DESISTENCIA_ENUM_VALUES);
          break;
        case RECIBO_CARTEIRA_DE_TRABALHO_ENTREGA.value:
        case RECIBO_CARTEIRA_DE_TRABALHO_DEVOLUCAO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_RECIBO_CARTEIRA_TRABALHO_ENUM_VALUES);
          break;
        case RECIBO_PRO_LABORE.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_RECIBO_PROLABORE_ENUM_VALUES);
          break;
        case DECLARACAO_OPCAO_FGTS.value:
          setIsTagTermos(CUSTUMIZACAO_TERMOS_TAGS_OPCAO_FGTS_ENUM_VALUES);
          break;
        case CONTRATO_TRABALHO_TEMPO_DETERMINADO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_CONTRATO_TAB_TEMPO_DETERMINADO_ENUM_VALUES);
          break;
        case AVISO_FERIAS.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_AVISO_FERIAS_ENUM_VALUES);
          break;
        case ABONO_PECUNIARIO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_ABONO_PECUNIARIO_ENUM_VALUES);
          break;
        case CARTA_PREPOSICAO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_CARTA_PREPOSICAO_ENUM_VALUES);
          break;
        case CARTA_ADVERTENCIA.value:
        case CARTA_ADVERTENCIA_FALTA.value:
          setIsTagTermos(CUSTUMIZACAO_TERMOS_TAGS_CARTA_ADVERTENCIA_ENUM_VALUES);
          break;
        case CONTRATO_ESTAGIO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_CONTRATO_ESTAGIO_ENUM_VALUES);
          break;
        case DECLARACAO_MAIS_DE_UM_VINCULO_EMPREGATICIO.value:
        case DECLARACAO_MAIS_DE_UM_VINCULO_EMPREGATICIO_AUTOMATIZADO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_DECLARACAO_OUTRO_VINCULO_EMPREGATICIO_ENUM_VALUES);
          break;
        case CARTA_SUSPENSAO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_CARTA_SUSPENSAO_ENUM_VALUES);
          break;
        case CARTA_RECOMENDACAO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_CARTA_RECOMENDACAO_ENUM_VALUES);
          break;
        case RECIBO_CESTA_BASICA.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_RECIBO_CESTA_BASICA_ENUM_VALUES);
          break;
        case EMPRESTIMO_EPI.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_EMPRESTIMOS_EPI_ENUM_VALUES);
          break;
        case CONTRATO_TRABALHO_TEMPO_PARCIAL.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_CONTRATO_TRAB_TEMPO_PARCIAL_ENUM_VALUES);
          break;
        case CONTRATO_TRABALHO_TEMPO_INDETERMINADO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_CONTRATO_TRAB_TEMPO_INDETERMINADO_ENUM_VALUES);
          break;
        case CONTRATO_TRABALHO_INTERMITENTE.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_CONTRATO_TRABALHO_IMTERMITENTE_ENUM_VALUES);
          break;
        case ACORDO_PRORROGACAO_HORA_EXTRA.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_ACORDO_PRORROGADO_HORA_EXTRA_VALUES);
          break;
        case VALE_REFEICAO_RECIBO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_VALE_REFEICAO_RECIBO_ENUM_VALUES);
          break;
        case VALE_ALIMENTACAO_RECIBO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_VALE_ALIMENTACAO_RECIBO_ENUM_VALUES);
          break;
        case CONTRATO_TRABALHO_MENOR_APRENDIZ.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_CONTRATO_TRABALHO_MENOR_APRENDIZ_ENUM_VALUES);
          break;
        case TERMO_QUITACAO_ANUAL.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_QUITACAO_ANUAL_ENUM_VALUES);
          break;
        case MSG_ANIVERSARIO.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_ANIVERSARIO_ENUM_VALUES);
          break;
        case MSG_CONTRA_CHEQUE.value:
          setIsTagTermos(CUSTOMIZACAO_TERMOS_TAGS_CONTRA_CHEQUE_ENUM_VALUES);
          break;
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    [formProps.getValues().tipo]
  );

  return (
    <WindowComponent title="Customização de Termos e Documentos">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline formProps={formProps} id="modelo" hidden={true} isPrependLabel={true} />
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 60 } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              placeholder="Informe a Descrição."
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 60 } }}
              id="titulo"
              isPrependLabel={true}
              label="Título"
              placeholder="Informe o Título do Termo."
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              id="tipo"
              label="Tipo de Termo"
              isPrependLabel={true}
              placeholder="Selecione um Tipo"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: CUSTOMIZACAO_TERMO_ENUM_VALUES,
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={formProps}
              id="conteudo"
              inputProps={{
                ref: e => {
                  formProps.register(e);
                  conteudoRef.current = e;
                },
                rows: 15,
              }}
              isPrependLabel={true}
              hideLabel={true}
              type={InputType.TEXT_AREA}
            />
          </Col>
        </Row>
        <Row className="mb-3">
          <Col xs={8}>
            <InputGroupInline
              id="tags"
              label="Tags"
              isPrependLabel={true}
              placeholder="Selecione uma tag"
              formProps={formProps}
              onChange={funcInsertTaginState}
              inputDropDownProps={{
                optionsProvider: isTagTermos,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
          <Col>
            <Button id={'addTermo'} color="primary" className="mr-2" onClick={funcInsertTagTermoWithComboBox}>
              Incluir
            </Button>
            <Button id={'restPadrao'} color="warning" onClick={restaurarTermoPadrao}>
              Restaurar Padrão
            </Button>
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default CustomizacaoTermosEditView;
